.App {
  text-align: center;
}

.body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.navbar {
  background-color:bisque;
  width: 100%;
  height: 80px;
  align-items: center;
  display: flex;
  justify-content: flex-end;
}

.links a {
  color:black;
  margin: 10px;
  text-align: center;
  margin-right: 50px;
}

.navbar .links {
  align-items: center;
  margin-right: 50px;
}

.navbar .user { 
  display: flex;
  justify-content: center;
}

.user p {
  font-size: 15px;
  margin-right: 10px;
  color: white;
}

.user img {
  font-size: 15px;
  border-radius: 50%;
}

input,
textarea {
  font-family: "Nunito", sans-serif;
}

.create-post {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

form {
  margin-top: 50px;
  padding: 30px 20px;
  width: 320px;
  border-radius: 7px;
  background-color: white;
  backdrop-filter: blur(5px);
  background-color: bisque;
  overflow: hidden;
}

input,
textarea {
  padding: 8px 10px;
  margin: 3px 8px 16px 8px;
  background-color: rgba(222, 239, 248, 0.877);
  border: 0px transparent;
  border-radius: 5px;
  color:black;
  font-size: 16px;
  z-index: 1;
}

.submitForm:hover {
  cursor: pointer;
}